.footer-links dt .arrow {
    position: absolute;
    right: 12px;
    top: 50%;
    width: 13px;
    height: 13px;
    display: inline-block;
    margin-top: -3px;
    transition: .3s ease;
    text-align: left;
    transform: rotate(45deg)
}

.footer-links dt .arrow:after,.footer-links dt .arrow:before {
    position: absolute;
    content: "";
    display: inline-block;
    width: 8px;
    height: 1px;
    background-color: #c9cdce;
    transition: .3s ease
}

.footer-links dt .arrow:after {
    position: absolute;
    transform: rotate(90deg);
    top: -4px;
    left: 4px
}

.footer-links dd {
    height: 0;
    overflow: hidden;
    transition: all .5s ease-in-out
}

.footer-links dd,.footer-links dd a {
    opacity: 0
