    font-size: 1.2rem;
    color: #666;
    transform: translateX(-50px)
}

.footer-links dd a:first-child {
    transition: all .5s ease-in-out .06s
}

.footer-links dd a:nth-child(2) {
    transition: all .5s ease-in-out .12s
}

.footer-links dd a:nth-child(3) {
    transition: all .5s ease-in-out .18s
}

.footer-links dd a:nth-child(4) {
    transition: all .5s ease-in-out .24s
}

.footer-links-active dt .arrow:before {
    transform: translate(8px)
}

.footer-links-active dt .arrow:after {
    transform: rotate(90deg) translate(8px)
}

.footer-links-active dd {
